@charset "UTF-8";
/**
    リセットと初期設定を両方記載
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");
*,
::before,
::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

html, body {
  padding: 0;
  margin: 0;
}

body {
  color: #333;
  font-size: 1.6rem;
  font-family: "Noto Sans JP", sans-serif;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6,
ul, li, ol, small, b, a,
strong, dl, dt, dd {
  padding: 0;
  margin: 0;
}

ul, ol {
  list-style: none;
}

.section__title {
  font-weight: bold;
  font-size: 4.8rem;
  text-align: center;
  margin: 10px 0;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .section__title {
    font-size: 3.2rem;
  }
}
@media screen and (max-width: 767px) {
  .section__title {
    font-size: 1.6rem;
  }
}
.section__area {
  padding: 100px 0;
}

@media screen and (max-width: 767px) {
  .section__area {
    padding: 50px 0;
  }
}
.inner {
  width: 1280px;
  max-width: 100%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #333;
}

.header {
  position: fixed;
  z-index: 500;
}

.header__logo {
  width: 200px;
  margin: auto 0;
  background: transparent;
}

.header__logo img {
  width: 100%;
}

.header .header__btn {
  width: 40px;
  height: 40px;
  top: 30px;
  right: 30px;
  transition: 0.6s;
  position: relative;
  position: fixed;
  z-index: 300;
}

.header .header__btn span {
  position: absolute;
  width: 100%;
  background-color: #000;
  height: 2px;
  transition: ease 0.4s;
  display: block;
}

.header .header__btn span:nth-of-type(1) {
  top: 0;
}

.header .header__btn span:nth-of-type(2) {
  top: 50%;
}

.header .header__btn span:nth-of-type(3) {
  bottom: 0;
}

.header .header__btn.active {
  left: 0;
}

.header .header__btn.active > span:nth-of-type(1) {
  top: 50%;
  transform: rotate(45deg);
}

.header .header__btn.active > span:nth-of-type(2) {
  display: none;
}

.header .header__btn.active > span:nth-of-type(3) {
  top: 50%;
  transform: rotate(-45deg);
}

.header__nav {
  display: none;
  position: fixed;
  top: 0;
  padding: 0;
  width: 100%;
  z-index: 200;
}

.header__nav.active {
  display: block;
}

.header__nav ul li {
  text-align: center;
}

.header__nav ul li a {
  color: #333;
  font-size: 1.6rem;
  padding: 30px 0;
  display: block;
  transition: 0.3s;
}

.header__nav ul li a:hover {
  text-decoration: underline;
  background: red;
  color: #fff;
}

.header__bg.active {
  z-index: 100;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(233, 233, 233, 0.611);
}

.top {
  position: relative;
}

.top img {
  height: 100%;
}

.top__text {
  font-size: 3.2rem;
  font-weight: bold;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .top__text {
    font-size: 1.8rem;
  }
}
@media screen and (max-width: 767px) {
  .top__text {
    font-size: 1rem;
  }
}
.top__text-up {
  position: absolute;
  top: 25%;
  left: 10%;
  transform: translateX(-10%);
  z-index: 12;
}

.top__text-down {
  position: absolute;
  left: 10%;
  top: 50%;
  transform: translateX(-10%);
  z-index: 12;
}

.top__bg {
  width: 600px;
  height: 300px;
  background: rgb(233, 222, 222);
  position: absolute;
  top: 48%;
  left: 0;
  z-index: 10;
  transform: translateY(-50%);
}

.top__bg1 {
  width: 200px;
  height: 200px;
  background: rgb(251, 246, 146);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .top__bg1 {
    flex-direction: column;
    width: 100px;
    height: 100px;
  }
}
@media screen and (max-width: 767px) {
  .top__bg1 {
    width: 50px;
    height: 50px;
  }
}
.top__bg2 {
  background: rgb(112, 188, 238);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  left: 11%;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .top__bg2 {
    left: 5%;
    flex-direction: column;
    width: 50px;
    height: 50px;
  }
}
@media screen and (max-width: 767px) {
  .top__bg2 {
    width: 25px;
    height: 25px;
    left: 4%;
  }
}
.footer {
  background: rgb(92, 179, 92);
}

.footer__menu {
  display: flex;
  flex-wrap: wrap;
}

.footer__menu li {
  width: 25%;
  text-align: center;
}

.footer__menu li a {
  display: inline-block;
  color: #fff;
  padding: 10px 4px;
  transition: 0.3s;
}

.footer__menu li a:hover {
  background: #fff;
  color: rgb(92, 179, 92);
  text-decoration: underline rgb(92, 179, 92);
}

.forte {
  background: rgb(244, 189, 101);
  width: 100%;
}

.forte__wrapper {
  width: 900px;
  padding: 0 10px;
}

@media screen and (max-width: 767px) {
  .forte__wrapper {
    width: 100%;
  }
}
.forte__subtitle {
  font-size: 1.6rem;
  letter-spacing: 1.5px;
  margin-top: 0.8rem;
}

@media screen and (max-width: 767px) {
  .forte__subtitle {
    font-size: 0.8rem;
    letter-spacing: 0;
  }
}
.forte__text {
  margin-left: 30px;
  font-size: 1.6rem;
  position: relative;
  padding-bottom: 4px;
}

.forte__text::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 4px;
  left: 0;
  bottom: 0;
  background: red;
  border-radius: 10px;
}

@media screen and (max-width: 767px) {
  .forte__text::after {
    height: 2px;
  }
}
@media screen and (max-width: 767px) {
  .forte__text {
    font-size: 0.8rem;
    margin-left: 12px;
  }
}
.about__cards {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 767px) {
  .about__cards {
    flex-direction: column;
  }
}
@media screen and (min-width: 1200px) {
  .about__card {
    width: calc(33.3333333333% - 36px);
    margin: 0 16px;
    padding: 10px 20px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .about__card {
    width: calc(50% - 28px);
    margin: 8px 12px;
    padding: 10px 16px;
  }
}
@media screen and (max-width: 767px) {
  .about__card {
    width: 100%;
    margin: 0;
    padding: 10px;
    margin-top: 0.8rem;
  }
}
.about__card__title {
  margin: 15px 0;
  text-align: center;
  padding-bottom: 16px;
  position: relative;
}

.about__card__title::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  background: red;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.illustration {
  background: url(../images/reLpbg04.jpg) no-repeat center center/cover;
}

.illustration__cards {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.illustration__card {
  width: calc(33.3333333333% - 36px);
  padding: 20px;
  margin: 16px;
}

@media screen and (min-width: 1200px) {
  .illustration__card {
    width: calc(33.3333333333% - 36px);
    margin: 0 16px;
    padding: 10px 20px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .illustration__card {
    width: calc(50% - 28px);
    margin: 8px 12px;
    padding: 10px 16px;
  }
}
@media screen and (max-width: 767px) {
  .illustration__card {
    width: 100%;
    margin: 0;
    padding: 10px;
    margin-top: 0.8rem;
  }
}
@media screen and (max-width: 767px) {
  .illustration__card__title {
    margin-top: 10px;
  }
}
@media screen and (max-width: 767px) {
  .illustration__card__text {
    margin-top: 10px;
    font-size: 0.8rem;
  }
}
.flow {
  background: url(../images/reLpbg01.jpg) center center/contain;
}

.flow__wrapper {
  width: 800px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .flow__wrapper {
    width: 80%;
  }
}
@media screen and (max-width: 767px) {
  .flow__wrapper {
    width: 90%;
  }
}
.flow__lists {
  margin: 0 auto;
  border: 8px double rgb(76, 153, 81);
  padding: 30px;
}

@media screen and (max-width: 767px) {
  .flow__lists {
    padding: 10px;
  }
}
.flow__list a {
  display: inline-block;
  padding: 4px 2px;
  margin-bottom: 10px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .flow__list a {
    font-size: 1rem;
  }
}
@media screen and (max-width: 767px) {
  .flow__list a {
    font-size: 0.8rem;
    margin-bottom: 4px;
  }
}
.flow__content {
  margin: 0 auto;
  margin-top: 80px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .flow__content {
    margin-top: 40px;
  }
}
@media screen and (max-width: 767px) {
  .flow__content {
    margin-top: 20px;
  }
}
.flow__dl {
  margin-top: 40px;
}

@media screen and (max-width: 767px) {
  .flow__dl {
    font-size: 0.8rem;
    margin-top: 20px;
  }
}
.flow__dl:nth-of-type(2n) {
  color: rgb(0, 83, 11);
}

.flow__dl:nth-of-type(2n+1) {
  color: rgb(51, 0, 105);
}

.flow__dl dt i {
  margin-right: 8px;
}

.flow__dl dd {
  color: #333;
}

@media screen and (max-width: 767px) {
  .flow__dl dd {
    font-size: 0.8rem;
  }
}
.dvisor__content {
  margin: 80px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

@media screen and (max-width: 767px) {
  .dvisor__content {
    flex-direction: column;
    margin: 0;
  }
}
.dvisor__item {
  margin-top: 30px;
  width: calc(50% - 36px);
  margin: 16px;
  padding: 20px;
}

@media screen and (max-width: 767px) {
  .dvisor__item {
    width: 100%;
    padding: 1px;
  }
}
@media screen and (max-width: 767px) {
  .dvisor__item p {
    font-size: 0.8rem;
  }
}
.dvisor__name {
  text-align: center;
}

@media screen and (max-width: 767px) {
  .dvisor__name {
    font-size: 1rem;
  }
}
.dvisor__img {
  width: 100%;
  margin-top: 30px;
}

.dvisor__img img {
  border-radius: 999px;
  transform: scale(-1, 1);
}

.jobchnger__content {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (min-width: 1200px) {
  .jobchnger__textbox {
    width: calc(33.3333333333% - 36px);
    margin: 0 16px;
    padding: 10px 20px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .jobchnger__textbox {
    width: calc(50% - 28px);
    margin: 8px 12px;
    padding: 10px 16px;
  }
}
@media screen and (max-width: 767px) {
  .jobchnger__textbox {
    width: 100%;
    margin: 0;
    padding: 10px;
    margin-top: 0.8rem;
  }
}
.pea__wrapper {
  width: 600px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .pea__wrapper {
    width: 100%;
  }
}
.pea__text {
  margin-top: 80px;
  margin: 0 auto;
}

.pea__content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 60px;
}

.pea__img {
  width: 50%;
  text-align: center;
}

.pea__img img {
  width: 50%;
}

.qa dl {
  width: 700px;
  margin: 20px auto 0;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .qa dl {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .qa dl {
    width: 100%;
  }
}
.qa dl dt {
  padding: 20px 4px;
}

.qa dl dd {
  padding: 20px 4px;
}

.contact__content {
  margin: 80px auto;
  width: 800px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .contact__content {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .contact__content {
    width: 100%;
  }
}
.contact__dl {
  margin-top: 32px;
  display: flex;
  flex-wrap: wrap;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .contact__dl {
    flex-direction: column;
  }
}
.contact__dl dt {
  width: 25%;
}

@media screen and (max-width: 767px) {
  .contact__dl dt {
    width: 100%;
  }
}
.contact__dl dt label {
  position: relative;
}

.contact__dl dt label::after {
  position: absolute;
  content: "必須";
  background: red;
  width: 30px;
  color: #fff;
  border-radius: 4px;
  margin-left: 4px;
  text-align: center;
  font-size: 1rem;
  top: 50%;
  transform: translateY(-50%);
}

.contact__dl dd {
  width: calc(100% - 200px);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .contact__dl dd {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .contact__dl dd {
    width: 100%;
  }
}
input [type=text], input[type=text],
input[type=tel], input[type=email] {
  appearance: none;
  border: none;
  outline: none;
  background: transparent;
  border: 1px solid red;
  width: 100%;
  height: 50px;
  border-radius: 999px;
  box-shadow: inset 2px -2px blue;
}

textarea {
  appearance: none;
  padding: 0;
  border: none;
  outline: none;
  background: transparent;
  border: 1px solid red;
  width: 100%;
  height: 300px;
  border-radius: 10px;
  box-shadow: inset 2px -2px blue;
  margin-top: 20px;
}

.btn {
  width: 320px;
  height: 40px;
  background: #fff;
  border: 2px solid green;
  display: inline-block;
  border-radius: 4px;
  transition: 0.3s;
  cursor: pointer;
  margin-top: 30px;
  margin: 30 auto;
}

.btn:hover {
  background: green;
  border: 2px solid #fff;
  color: #fff;
}